import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './App.css';

const initialState = {
  kilogram: "",
  pound: "",
  milligram: "",
}



class App extends Component{
  state = initialState;

  handleKilogram = event => {
    const kilogram = event.target.value;

    this.setState({
      kilogram,
      pound: kilogram * 2.204623,
      milligram: kilogram * 1000000
    });

      if(kilogram === ""){
      this.setState(initialState);
      }
  };

  handleMilligram = event => {
    const milligram = event.target.value;

    this.setState({
        kilogram: milligram / 1000000,
        pound: milligram * 2204623,
        milligram
      });

      if(milligram === ""){
      this.setState(initialState);
      }
  };
  
  handlePound = event => {
  const pound = event.target.value;
  
  this.setState({
        kilogram: pound / 2.204623,
        pound,
        milligram: pound / 2204623
      });

      if(pound === ""){
      this.setState(initialState);
      }
  };

  render() {
  return (
    <div className="App">
      <header className="App-header">
        <h2>
          Weight Converter
        </h2>
        <p>
          <label class="weight-label" >
            Kilogram (kg): <br></br>
            <input type="number" name="kilogram" size="10" min="0" value={this.state.kilogram} onChange={this.handleKilogram} />
          </label>
        </p>
        =
        <p>
          <label class="weight-label" >
            Pound (lb): <br></br>
            <input type="number" name="pound" size="10" min="0" value={this.state.pound} onChange={this.handlePound} />
          </label>
        </p>
        =
        <p>
          <label class="weight-label" >
            Milligram (mg): <br></br>
            <input type="number" name="milligram" size="10" min="0" value={this.state.milligram} onChange={this.handleMilligram} />
          </label>
        </p>
      </header>
    </div>
  );
}
}
export default App;
